*{
  margin: 0;
  padding: 0;  
}
body{
  background-color: #FFFAF0;
}


.app-wrapper{
  margin: 0 auto;
  width: 80%;
  display: grid;
  grid-template-rows: 60PX 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas: 
  "header header"
  "nav content"
  "friends content";
  background-color: #FFFAF0;
}

.app-wrapper-content{
  grid-area: content;
}





